<template>
  <v-card
    height="100%"
    min-height="240px"
  >
    <div
      id="docentes-universitarios-cantidad"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Cantidad de Docentes Universitarios.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('docentes-universitarios-cantidad')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los/as Docentes.</v-card-subtitle>
      <v-card-text>
        <v-row
          justify="space-between"
          align="center"
        >
          <v-col
            class="display-1"
            cols="8"
          >
            {{ docuniversitarios }} Docentes Universitarios
          </v-col>
          <v-col cols="2">
            <v-avatar
              color="info"
              rounded
              :size="60"
            >
              <v-icon
                dark
                large
              >
                mdi-human-male-board-poll
              </v-icon>
            </v-avatar>
          </v-col>
        </v-row>
      </v-card-text>
    </div>
  </v-card>
</template>

<script>
import { mapGetters } from 'vuex'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapGetters(['docentes']),
    docuniversitarios () {
      return [...new Set(this.docentes.flatMap(docente => docente.legajo))].length
    }
  },
  methods: {
    downloadIndicador (indicador) {
      this.$store.dispatch('createPng', indicador)
    }
  }
}
</script>
